<template>
    <div class="container d-flex justify-center">
        <v-btn v-for="({ title, key }) in optionNames"
               :key="key"
               @click="toggle(key)"
               depressed large tile
               :text="!chosen(key)"
               :outlined="chosen(key)"
        >{{ title }}</v-btn>
        <v-btn v-if="hasAll"
               @click="toggleAll"
               depressed large tile
               :text="!areAllChosen"
               :outlined="areAllChosen"
        >All</v-btn>
    </div>
</template>

<script>
export default {
    name: "OptionsChooser",
    props: {
        optionNames: { type: Array, default: () => [] },
        hasAll: { type: Boolean },
        chosenDictionaries: { default: () => [] },
    },
    data() {
        return {
            chosenKeys: [],
            activeColor: 'blue-grey',
            activeAllColor: 'cyan darken-2'
        }
    },
    computed: {
        areAllChosen() {
            return this.chosenKeys.length === this.optionNames.length;
        }
    },
    watch: {
        optionNames: {
            deep: true,
            handler() {
                this.chosenKeys = this.optionNames.filter(({ isChosen }) => isChosen).map(({ value }) => value);
            }
        }
    },
    methods: {
        chosen(key) {
            return this.chosenKeys.includes(key);
        },
        toggle(key) {
            if (this.chosenKeys.includes(key)) {
                this.chosenKeys = this.chosenKeys.filter((el) => el !== key);
            } else {
                this.chosenKeys = [...this.chosenKeys, key];
            }
            this.emitValues();
        },
        toggleAll() {
            if (this.chosenKeys.length < this.optionNames.length) {
                this.chosenKeys = this.optionNames.map(({ key }) => key);
            } else {
                this.chosenKeys = [];
            }
            this.emitValues();
        },
        emitValues() {
            this.$emit('chosen-dictionaries', this.chosenKeys);
        }
    },
    created() {
        this.chosenKeys = this.chosenDictionaries;
    }
}
</script>

<style scoped>
::v-deep .v-btn__content {
    width: 7rem;
}
::v-deep .v-btn:not(.v-btn--outlined) {
    border: thin solid white;
}
</style>
