<template>
    <div>
        <v-container class="searcher-wrapper">
            <Searcher :searcher-string="searcherString"
                      :corpus-name="corpusName"
                      @searcher-string="onSearcherString"
            ></Searcher>
        </v-container>
        <OptionsChooser :option-names="dicts"
                        :chosen-dictionaries="chosenDictionaries"
                        has-all
                        @chosen-dictionaries="onChosenDictionaries"
        ></OptionsChooser>
        <v-container v-for="dict of matchingDictLinesArr" :key="dict.key">
            <h1>{{ dict.title }}</h1>
            <SearchResults v-if="dict.lines.length"
                           :lines-matching="dict.lines"
                           :searcher-string="searcherString"
                           :disable-infinite-scroll="true"
            ></SearchResults>
        </v-container>
    </div>
</template>

<script>
import Searcher from '@/components/corpusSimple/Searcher';
import SearchResults from '@/components/corpusSimple/SearchResults';
import OptionsChooser from '@/components/OptionsChooser';

export default {
    name: "DictsSearch",
    components: {
        Searcher,
        SearchResults,
        OptionsChooser
    },
    props: ['corpusName'],
    data() {
        return {
            searcherString: '',
            disableInfiniteScroll: false,
            chosenDictionaries: [],
        }
    },
    computed: {
        dicts() {
            return this.$store.getters.dictsByCorpusNames(this.corpusName);
        },
        matchingDictLinesArr() {
            return this.$store.getters.matchingDictLinesArrByCorpus(this.corpusName)
                .filter(({ key }) => this.chosenDictionaries.includes(key));
        },
        query() {
            const searchPart = this.searcherString ? { search: this.searcherString } : {};
            const dictsPart = this.chosenDictionaries ? { dicts: this.chosenDictionaries.join('_') } : {};
            return { ...dictsPart, ...searchPart };
        }
    },
    watch: {
        query: {
            deep: true,
            handler(query) {
                if (_.isEqual(query, this.$route.query)) {
                    return;
                }
                this.$router.push({ query });
            },
        },
    },
    methods: {
        onSearcherString(searcherString) {
            this.searcherString = searcherString;
            this.$store.commit('setSearcherString', {
                corpusName: this.corpusName,
                searcherString: this.searcherString,
            });
        },
        onChosenDictionaries(value) {
            this.chosenDictionaries = value;
        },
    },
    created() {
        this.$store.commit('initializeDictData', this.corpusName);
        this.chosenDictionaries = this.$store.getters.dictsByCorpusNames(this.corpusName)
                .filter(({ isChosen }) => isChosen).map(({ key }) => key);
        this.$store.commit('setSearcherString', {
            corpusName: this.corpusName,
            searcherString: this.searcherString,
        });
    },
    mounted() {
        if (this.$route.query.search) {
            this.onSearcherString(this.$route.query.search);
        }
    },
}
</script>

